<template>
  <div class="privacy" style="padding-top: 44px;">
    <back-header :title="content.title" />
    <scroll ref="scroll">
      <div class="content" v-html="content.content"></div>
    </scroll>
  </div>
</template>

<script>
import BackHeader from '../../components/back-header/back-header'
import Scroll from '../../components/scroll/scroll'
import { post } from '../../common/js/http'

export default {
  name: 'privacy',
  components: {
    BackHeader,
    Scroll
  },
  data() {
    return {
      content: ''
    }
  },
  created() {
    post('/webapi/Protocal/getProtocal', {
      type: this.$route.params.type
    })
      .then(({ data: { data } }) => {
        this.content = data
        this.$nextTick(() => {
          this.$refs.scroll.refresh()
        })
      })
  }
}
</script>

<style scoped lang="stylus">
.privacy
  box-sizing border-box
  .content
    padding 20px 30px
    line-height 40px
    font-size 24px
    ::v-deep
      li
        padding 3px 0
      strong
        font-weight bold
</style>
